<template>
  <!-- 导航菜单组件 黑色  -->
  <q-toolbar class="bg-black text-white">
    <q-fab flat round dense icon="menu" class="q-mr-sm" direction="down">
      <q-fab-action
        color="amber"
        text-color="black"
        @click="onClick"
        icon="mail"
      />
      <q-fab-action
        color="amber"
        text-color="black"
        @click="onClick"
        icon="alarm"
      />
      <q-fab-action
        color="amber"
        text-color="black"
        @click="open('top')"
        icon="music_note"
      />
    </q-fab>

    <!-- 图标 -->
    <!-- https://cdn.quasar.dev/logo-v2/svg/logo-mono-white.svg -->
    <q-avatar>
      <img
        src="@/assets/logo/snow_57.svg"
        :class="[isRotate ? 'rotate' : 'rotate1']"
        @mouseover="isRotate = true"
        @mouseleave="isRotate = false"
        @click="goto('Main')"
      />
    </q-avatar>

    <!-- 网站标题 -->

    <q-toolbar-title @click="goto('Main')">Ice-Dragon</q-toolbar-title>

    <!-- 导航栏按钮 -->
    <q-btn-group push>
      <q-btn push label="主页" icon="gite" :to="menu[0]" />
      <q-btn push label="博客" icon="laptop_chromebook" :to="menu[4]" />
      <!-- <q-btn push label="作品" icon="view_sidebar" :to="menu[3]" />
      <q-btn push label="番剧" icon="polymer" :to="menu[1]" />

      <q-btn push label="事记" icon="receipt_long" :to="menu[5]" />
      <q-btn push label="导航" icon="webhook" :to="menu[7]" /> -->
      <q-btn push label="工具" icon="webhook" :to="menu[2]" />
      <q-btn push label="关于我" icon="api" :to="menu[6]" />
      <q-btn push label="虚无之境" icon="all_inclusive" :to="menu[8]" />
    </q-btn-group>
    <!-- 空格 -->
    <q-space></q-space>
    <!-- <q-space></q-space>
    <q-space></q-space>
    <q-space></q-space> -->
    <!-- 一句话 -->
    <b style="position: relative; font-family: geom-graphic; font-size: 15px">
      {{ word }}
    </b>
    <!-- 一句话切换按钮 -->
    <q-btn flat round dense icon="local_fire_department" />

    <!-- 非加载显示组件 -->
    <q-dialog v-model="dialog" :position="position">
      <q-card style="width: 350px">
        <q-linear-progress :value="music_progress" color="blue" />

        <q-card-section class="row items-center no-wrap">
          <div>
            <div class="text-weight-bold">{{ song }}</div>
            <div class="text-grey">{{ singer }}</div>
          </div>

          <q-space />

          <q-btn flat round icon="fast_rewind" />
          <q-btn flat round icon="pause" />
          <q-btn flat round icon="fast_forward" />
        </q-card-section>
      </q-card>
    </q-dialog>
  </q-toolbar>
</template>

<script>
import { ref } from "vue";
export default {
  name: "MainMenu",

  components: {},
  data() {
    return {
      isRotate: false,
      word: "你 说 机 械 做 的 心 能 梦 到 电 子 蝴 蝶 吗 ？",
      menu: [
        "/main",
        "/anime",
        "/work",
        "/show",
        "/blog",
        "/bbs",
        "/myself",
        "/navigater",
        "/library",
      ],
      music_progress: "",
      song: "",
      singer: "",
    };
  },

  methods: {
    goto(index) {
      this.$router.push(index);
    },
  },
  setup() {
    var url = require("../assets/back.jpg");
    const dialog = ref(false);
    const position = ref("top");
    return {
      url,
      dialog,
      position,

      open(pos) {
        position.value = pos;
        dialog.value = true;
      },
    };
  },
};
</script>

<style lang="scss" scope>
.rotate {
  transition: all 1.5s;
}
.rotate1 {
  transform: rotate(360deg);
  -ms-transform: rotate(360deg); /* IE 9 */
  -moz-transform: rotate(360deg); /* Firefox */
  -webkit-transform: rotate(360deg); /* Safari 和 Chrome */
  -o-transform: rotate(360deg); /* Opera */
  transition: all 1.5s;
}
</style>
