import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'
import BgLogin from '../views/backgroundSystem/Login&Logup.vue'
import BgHome from '../views/backgroundSystem/Home.vue'

export const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect: '/main',
    children:[
      {
        path: '/main',
        component: ()=> import ('../views/ForegroundSystem/MainPage.vue'),
        hidden: true,
      },
      {
        path: '/anime',
        component: ()=> import ('../views/ForegroundSystem/Anime.vue'),
        hidden: true,
      },
      {
        path: '/work',
        component: ()=> import ('../views/ForegroundSystem/Work.vue'),
        hidden: true,
      },
      {
        path: '/show',
        component: ()=> import ('../views/ForegroundSystem/Show.vue'),
        hidden: true,
      },
      {
        path: '/blog',
        component: ()=> import ('../views/ForegroundSystem/Blog.vue'),
        hidden: true,
        redirect:'/blog/main',
        children:[
          {
            path:'/blog/main',
            component:()=>import('../views/ForegroundSystem/Blog/BlogHome.vue'),
            hidden:true,
            
          },
          {
            path:'/blog/list',
            component:()=>import('../views/ForegroundSystem/Blog/BlogList.vue'),
            hidden:true,
          }
        ]
      },
      {
        path: '/bbs',
        component: ()=> import ('../views/ForegroundSystem/BBS.vue'),
        hidden: true,
      },
      {
        path: '/myself',
        component: ()=> import ('../views/ForegroundSystem/Myself.vue'),
        hidden: true,
      },
      {
        path: '/navigater',
        component: ()=> import ('../views/ForegroundSystem/Navigater.vue'),
        hidden: true,
      },
      {
        path: '/library',
        component: ()=> import ('../views/ForegroundSystem/Library.vue'),
        hidden: true,
      },

    ]
  },
  {
    path: '/bg/yorha/login',
    name: 'BgLogin',
    component: BgLogin,
  },
  {
    path: '/bg/yorha/Home',
    name: 'BgHome',
    component: BgHome,
    meta:{requireAuth:true}, 
  }

]



const router = createRouter({
  history: createWebHashHistory(),
  routes,

})


router.beforeEach((to, from,next) => {
  let regex = '/bg/yorha/+';
  let have = to.path.search(regex);
  if (have==0 && to.path === '/bg/yorha/login') next();
  const tokenStr = window.sessionStorage.getItem('token');
  if (have ==0 && !tokenStr) next('/bg/yorha/login');
  return next();

})

export default router
